import {
  DeleteOutlined,
  EditOutlined,
  LoadingOutlined,
  PlusCircleOutlined,
} from "@ant-design/icons";
import { Modal, Space, Spin, Table, Button } from "antd";
import "antd/dist/antd.css";
import moment from "moment";
import React, { useEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { COLOR } from "ultis/functions";
import { PAGE } from "../constant";
import "../dashboard.css";
import {
  DeleteTrip,
  GetMapSeat,
  GetTripList,
  SetCurrentPage,
  GetCitiesTrip,
} from "../redux/actions";
import { getColumnSearchProps } from "./searchInput";
import TripDateDetail from "./tripDateDetail";
import AddTripPage from "./addTrip";

const loadingIcon = (
  <LoadingOutlined style={{ fontSize: 30, color: COLOR.primary }} spin />
);

function TripList() {
  const tripList = useSelector((state) => state.Dashboard.tripList);
  const isLoading = useSelector((state) => state.Dashboard.isLoading);
  const accountType = useSelector((state) => state.Dashboard.accountType);
  const detailPage = useSelector((state) => state.Dashboard.detailPage);
  const dispatch = useDispatch();
  const [searchText, setSearchText] = useState("");
  const [searchedColumn, setSearchColumn] = useState("");
  const refInput = useRef();

  const getTripList = () => {
    if (accountType && accountType !== "admin") {
      dispatch(GetTripList.get({ busOperatorId: accountType }));
    } else {
      dispatch(GetTripList.get());
    }
  };
  useEffect(() => {
    getTripList();
  }, []);

  const handleEdit = (value, record) => {
    dispatch(
      GetMapSeat.get({
        tripId: record.tripId,
        busType: record.busType,
        date: moment(new Date()).format("DD/MM/YYYY"),
      })
    );
    dispatch(
      SetCurrentPage.get({
        currentPage: PAGE.TRIP_LIST,
        detailPage: PAGE.TRIP_DETAIL,
      })
    );
  };

  const handleReset = () => {
    getTripList();
    dispatch(SetCurrentPage.get({ currentPage: PAGE.TRIP_LIST }));
  };

  const handleDelete = (record) => {
    Modal.confirm({
      title: "Xc nhn",
      icon: <DeleteOutlined style={{ color: COLOR.primary }} />,
      content: "Bn xc nhn xa chuyn ny?",
      okText: "ng ",
      cancelText: "Hy b",
      centered: true,
      okButtonProps: { style: { backgroundColor: COLOR.primary } },
      onOk: () => {
        dispatch(DeleteTrip.get({ tripId: record.tripId }));
        Modal.destroyAll();
      },
    });
  };

  const onAddNewTrip = () => {
    dispatch(GetCitiesTrip.get());
  };

  const tripsColumns = [
    {
      ...getColumnSearchProps(
        "busOperator",
        "Nhp tn nh xe",
        searchText,
        setSearchText,
        searchedColumn,
        setSearchColumn,
        refInput
      ),
      title: "Nh xe",
      dataIndex: "busOperator",
      key: "busOperator",
    },
    {
      title: "M chuyn",
      dataIndex: "tripId",
      sorter: (a, b) => a.tripId.localeCompare(b.tripId),
    },
    {
      title: "Loi xe",
      dataIndex: "busTypeTitle",
      key: "busTypeTitle",
      ...getColumnSearchProps(
        "busTypeTitle",
        "Nhp loi xe",
        searchText,
        setSearchText,
        searchedColumn,
        setSearchColumn,
        refInput
      ),
    },
    {
      title: "im khi hnh",
      dataIndex: "from",
      key: "from",
      ...getColumnSearchProps(
        "from",
        "Nhp im khi hnh",
        searchText,
        setSearchText,
        searchedColumn,
        setSearchColumn,
        refInput
      ),
    },
    {
      title: "im n",
      dataIndex: "to",
      key: "to",
      ...getColumnSearchProps(
        "to",
        "Nhp im in",
        searchText,
        setSearchText,
        searchedColumn,
        setSearchColumn,
        refInput
      ),
    },
    {
      title: "Thi im khi hnh",
      dataIndex: "timeStart",
      sorter: (a, b) => a.timeStart.localeCompare(b.timeStart),
    },
    {
      title: "Tc v",
      key: "action",
      render: (value, record) => {
        return (
          <Space>
            <EditOutlined
              style={{ fontSize: 20 }}
              onClick={() => handleEdit(value, record)}
            />
            <DeleteOutlined
              style={{ fontSize: 20, color: "#FF0000" }}
              onClick={() => handleDelete(record)}
            />
          </Space>
        );
      },
    },
  ];

  if (isLoading) {
    return (
      <div className="chooseContainer">
        <Spin indicator={loadingIcon} />
      </div>
    );
  }
  if (detailPage === PAGE.TRIP_DETAIL) {
    return <TripDateDetail handleReset={handleReset} />;
  }
  if (detailPage === PAGE.ADD_TRIP) {
    return <AddTripPage />;
  }
  return (
    <div className="chooseContainer">
      <span className="titleTopic">Danh sch chuyn xe</span>
      {accountType && accountType !== "admin" && (
        <Button
          type="primary"
          icon={<PlusCircleOutlined />}
          style={{ width: 200, marginBottom: 32 }}
          onClick={() => onAddNewTrip()}
        >
          Thm chuyn xe mi
        </Button>
      )}
      <Table columns={tripsColumns} dataSource={tripList} />
    </div>
  );
}

export default TripList;
